.image{
    /* image container div */
    width: 100%;
    height: 200px;
    background-color: #444;
    overflow: hidden;
    object-fit: cover;
    margin-bottom: 2em;
    position: relative;
}

.image-credit{
    text-align: right;
    font-style: italic;
    color: #fff;
    font-size: 0.8em;
    position: absolute;
    bottom: 0;
    right: 0;
    padding: 0.2rem;
    background-color: rgba(0.2,0.2,0.2,0.5);
}

.image img {
    width: 100%;
    position: relative;
    top: 50%;
    left: 50%;
